import React from 'react';
import 'semantic-ui-css/semantic.min.css'
import {Image, Icon, Menu} from 'semantic-ui-react'

function AboutHeader() {
    return (
        <div>
          <div className="ui top fixed">
            <Menu className="secondary" stackable={true}>
              <div className = "ui container">
                <Menu.Header>
                  <Image className="ui tiny" fluid={true} src="/favicon.png"></Image>
                </Menu.Header>
                <Menu.Item
                  name='About Me'
                ></Menu.Item>
                <Menu.Item
                  name='Projects'
                ></Menu.Item>
                <Menu.Item
                  name='Contact'
                ></Menu.Item>
                <Menu.Item
                  name='Download Resume'
                  position="right"
                ></Menu.Item>
                <Menu.Item>
                  <Icon name="github" size="large"></Icon>
                </Menu.Item>
              </div>
            </Menu>              
          </div>
      </div>
    )
}

export default AboutHeader;
